import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AppPreloader } from './app-routing-loader';
import { AuthLayoutComponent } from './auth/auth-layout/auth-layout.component';
import { AuthGuard } from './components/Guard/auth.guard';
import { SidenavComponent } from './layout/sidenav/sidenav.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'auth/login',
    pathMatch: 'full',
  },
  {
  path: '',
  component: SidenavComponent,
  children: [
    {
      path: 'welcome',
      loadChildren: () =>
        import('src/app/pages/welcome/welcome.module').then(m => m.WelcomeModule),
    },
    {
      canActivate:[AuthGuard],
      path:'bankaccount',
      loadChildren:()=> import('src/app/pages/bankaccount/bankaccount.module').then(m=>m.BankaccountModule)
    },
    {
      canActivate:[AuthGuard],
      path:'broker',
      loadChildren:()=>import('src/app/pages/company/company.module').then(m=>m.CompanyModule)
    },
    {
      canActivate:[AuthGuard],
      path:'motortype',
      loadChildren:()=>import('src/app/pages/motortype/motortype.module').then(m=>m.MotortypeModule)
    },
    {
      canActivate:[AuthGuard],
      path:'outstandings',
      loadChildren:()=>import('src/app/pages/outstanding/outstanding.module').then(m=>m.OutstandingModule)
    },
    {
      canActivate:[AuthGuard],
      path:'salesmanagement',
      loadChildren:()=>import('src/app/pages/sales/sales.module').then(m=>m.SalesModule)
    },
    {
      canActivate:[AuthGuard],
      path:'transaction',
      loadChildren:()=>import('src/app/pages/transactions/transactions.module').then(m=>m.TransactionsModule)
    },
    {
      canActivate:[AuthGuard],
      path:'teammember',
      loadChildren:()=>import('src/app/pages/user/user.module').then(m=>m.UserModule)
    },
    {
      canActivate:[AuthGuard],
      path:'vehicletype',
      loadChildren:()=>import('src/app/pages/vehiclemodel/vehiclemodel.module').then(m=>m.VehiclemodelModule)
    },
    {
      canActivate:[AuthGuard],
      path:'agent',
      loadChildren:()=>import('src/app/pages/agent/agent.module').then(m=>m.AgentModule)
    },
    {
      canActivate:[AuthGuard],
      path:'invoice',
      loadChildren:()=>import('src/app/pages/invoice/invoice.module').then(m=>m.InvoiceModule)
    },
    {
      canActivate:[AuthGuard],
      path:'statements',
      loadChildren:()=>import('src/app/pages/statements/statements.module').then(m=>m.StatementsModule)
    },
    {
      canActivate:[AuthGuard],
      path:'dashboard',
      loadChildren:()=>import('src/app/pages/dashboard/dashboard.module').then(m=>m.DashboardModule)
    },
    {
      canActivate:[AuthGuard],
      path:'user',
      loadChildren:()=>import('src/app/pages/user/user.module').then(m=>m.UserModule)
    }
    ,
    {
      canActivate:[AuthGuard],
      path:'expenses',
      loadChildren:()=>import('src/app/pages/expense/expense.module').then(m=>m.ExpenseModule)
    },
    {
      //canActivate:[AuthGuard],
      path:'branch-management',
      loadChildren:()=>import('src/app/pages/branch/branch.module').then(m=>m.BranchModule)
    }
  ]
  },
  {
    path:'',
    component:AuthLayoutComponent,
    children:[
      {
        path:'auth',
        loadChildren:()=> import('src/app/auth/auth.module').then(m=>m.AuthModule)
      }
    ]
  }
];

@NgModule({

  imports: [RouterModule.forRoot(routes,{
    useHash:true,
    preloadingStrategy:AppPreloader
  })],
  providers:[AppPreloader],
  exports: [RouterModule]
})
export class AppRoutingModule { }
