import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { BehaviorSubject } from 'rxjs/internal/BehaviorSubject';
import { Observable } from 'rxjs/internal/Observable';
import { map } from 'rxjs/internal/operators/map';
import { BaseResponse } from 'src/app/Model/base-response';
import { API_ENDPOINTS, API_URL } from 'src/app/Model/Global';
import { RoleClaimsDTO } from 'src/app/Model/roleClaimsDTO';
import { RoleDTO } from 'src/app/Model/RoleDTO';
import { UserDTO } from 'src/app/Model/userDTO';
import { AlertService } from '../alert.service';
import { BaseApiHandlerService } from '../base-api-handler.service';
import { NzMessageService } from 'ng-zorro-antd/message';
import jwt_decode from 'jwt-decode'
import { PermissionService } from '../permission.service';
@Injectable({
  providedIn: 'root'
})
export class AuthService extends BaseApiHandlerService {

  roleListObserver$: Observable<RoleDTO[]>;
  private roleListSubject$: BehaviorSubject<RoleDTO[]> = new BehaviorSubject<RoleDTO[]>(undefined);
  private userSubject: BehaviorSubject<any>;
  public user: Observable<any>;
  returnUrl: any
  tokenPayload: unknown;
  userClaims: string[];
  constructor(private http: HttpClient,
    private alert:AlertService,
    private _permission :PermissionService,
    private message:NzMessageService,
    private router:Router) {
    super(http);
    this.roleListObserver$ = this.roleListSubject$.asObservable();
    this.userSubject = new BehaviorSubject<UserDTO>(undefined);
    this.user = this.userSubject.asObservable();
  }

  CreateRoleWithClaims(data: RoleClaimsDTO) {
    return this.Post(data, API_URL + API_ENDPOINTS.Auth + "/CreateRoleWithClaims");
  }

  UpdateRoleWithClaims(roleId, data: RoleClaimsDTO) {
    return this.Update(roleId, data, API_URL + API_ENDPOINTS.Auth + "/UpdateRoleWithClaims");
  }

  GetAllRoles() {
    return this.GetAll(API_URL + API_ENDPOINTS.Auth + "/GetAllRoles").subscribe(res => {
      this.roleListSubject$.next(res.dynamicResult);
    });
  }
  OnBoardingProcess(data)
  {
    return this.Post(data, API_URL + API_ENDPOINTS.Auth + "/OnBoardingProcess");
  }
  login(data): Observable<any> {
    return this.Post(data, API_URL + API_ENDPOINTS.Auth )
      .pipe(
        map(response => {

          if (response && response.isSuccessfull) {


            localStorage.setItem('accessToken', response.dynamicResult.token)
            this.message.success("You're Authenticated, Redirecting you to portal");
            this.tokenPayload = this.decodeToken(response.dynamicResult.token)
            this.userClaims = Object.keys(this.tokenPayload)
            this.userClaims = this.userClaims.map(x => x.toLowerCase())
            var roles = this.userClaims.slice(3,16);

            roles.forEach(role=>{
              this._permission.setPermissions(role,this.tokenPayload);
            });
          //  this.message.clear();
            this.alert.success('You have successfully logged in.')
            this.router.navigate(['/welcome'])
            return response
          }
          else{
            this.alert.success('Incorrect Password')
          }
        }))
  }

  generatePasswordResetToken(email: string): Observable<BaseResponse> {
    return this.http.get<BaseResponse>(`${API_URL}api/auth/GenerateForgotPasswordToken?email=${email}`)
  }

  resetPasswordResetWithToken(data: any): Observable<BaseResponse> {
    return this.http.post<BaseResponse>(`${API_URL}api/auth/ResetPasswordWithToken`, data);
  }

  createNewPassword(data: any): Observable<BaseResponse> {
    return this.http.post<BaseResponse>(`${API_URL}api/auth/CreateNewPassword`, data);
  }
  decodeToken(token: string) {
    try {
      return jwt_decode(token)
    }
    catch {
      return null
    }
  }
}


