import { Injectable } from '@angular/core';
import Swal from 'sweetalert2'


@Injectable({
  providedIn: 'root'
})
export class AlertService {

  constructor() { }

  success(msg: string) {
    Swal.fire({
      title: 'Success!',
      text: msg,
      icon: 'success',
      timer: 3000,
      confirmButtonText: '  Ok  ',
      confirmButtonColor: '#5BAB54',
    })
  }

  error(msg: string) {
    Swal.fire({
      title: 'Error!',
      text: msg,
      icon: 'error',
      timer: 3000,
      confirmButtonText: '  Ok  ',
      confirmButtonColor: '#F15E5E',
    })
  }

  delete(msg?: string) {
    return Swal.fire({
      title: 'Are you sure?',
      text: `Do you really want to delete this records? \n This process cannot be undone.`,
      icon: 'error',
      showCancelButton: true,
      confirmButtonText: 'Yes',
      confirmButtonColor: '#F15E5E',
    })
  }

  confirm(title: string, msg?: string) {
    return Swal.fire({
      title: title,
      text: msg,
      icon: 'info',
      showCancelButton: true,
      confirmButtonText: 'Yes',
      cancelButtonText: 'No',
      confirmButtonColor: '#109CF1',
    })
  }

  warn(msg: string) {
    Swal.fire({
      title: 'Cannot Perform!',
      text: msg,
      icon: 'warning',
      timer: 3000,
      confirmButtonText: '  Ok  ',
      confirmButtonColor: '#FFB946',
    })
  }

  info(msg: string) {
    Swal.fire({
      title: 'Information',
      text: msg,
      icon: 'info',
      timer: 3000,
      confirmButtonText: '  Ok  ',
      confirmButtonColor: '#1292d5',
    })
  }
}
